import { useDispatch, useSelector } from "react-redux";
import { useEffect, useState } from "react";
import { getTeamDyReport } from "../../store/actions/dailyYield";
import moment from "moment";
import { CustomTable } from "../../components/CustomTable";

export const TeamDY = () => {
  const bonusDyReport = useSelector((state) => state?.dailyYield?.teamDyReport);
  const dispatch = useDispatch();
  const user = localStorage.getItem("user");
  const [dataState, setDateState] = useState([]);

  const getAllData = () => {
    if (user) {
      let ress = JSON.parse(user);
      let uId = ress?.user_id;
      dispatch(getTeamDyReport(uId));
    }
  };
  useEffect(() => {
    getAllData();
  }, []);

  useEffect(() => {
    let arr = [];
    bonusDyReport.forEach((item, index) => {
      arr.push({
        sNo: index + 1,
        from_id: item?.user_id,
        levelcount: item?.Leveltype,
        totalincome: item?.Level_income,
        date: moment(item?.edate.replace("" , "")).format("M/D/YYYY h:m:s A"),
        // date:
        //   moment(item?.top_update).format("M/D/YYYY h:m:s A") !=
        //   "Invalid date"
        //     ? moment(item?.top_update).format("M/D/YYYY h:m:s A")
        //     : "Inactive",
      });
    });
    setDateState([...arr]);
  }, [bonusDyReport]);
  // console.log("state", bonusDyReport);
  return (
    <>
      <div class="content-wrapper">
        <div class="grid grid-1">
          <div class="section-heading">
            <h2>Team DY</h2>
          </div>
          <CustomTable
            columns={[
              { title: "S.No", field: "sNo" },
              { title: "From Id", field: "from_id" },
              { title: "Level", field: "levelcount" },
              { title: "Income", field: "totalincome" },
              { title: "Date", field: "date" },
            ]}
            data={[...dataState]}
            title=""
            toolbar={false}
          />
        </div>
      </div>
      <div class="clearfix">
        <br />
      </div>

      <br />
      <br />
      <div class="footer-section">
        Copyright © 2022 Yeepule. All Rights Reserved.
      </div>
      <link
        rel="stylesheet"
        type="text/css"
        href="assets/css/2.d34346ea.chunk.css"
      />
      <link
        rel="stylesheet"
        type="text/css"
        href="assets/css/main.f70df022.chunk.css"
      />
    </>
  );
};
